// Dimensions
$max: 100%;
$half: calc($max / 2);
$third: calc($max / 3);
$quarter: calc($max / 4);
$tenth: calc($max / 10);
$rm: 0.8rem;
$px: 8px;

// Fonts
//$fontA: 'Poppins', sans-serif;
//$font3: "DejaVu Sans", sans-serif;

// Spacings:
// margins, line heights
$m: calc($rm / 2);
$m1: $rm;
$m2: calc($rm * 2);
$m3: calc($rm * 3);
$m4: calc($rm * 4);
$m5: calc($rm * 5);
$m6: calc($rm * 6);
$m7: calc($rm * 7);
$m8: calc($rm * 8);
$m9: calc($rm * 9);
$m10: calc($rm * 10);

// Spacings:
// letters spacings
$ls: 0.025rem;
$ls1: 0.05rem;
$ls2: 0.075rem;
$ls3: 0.1rem;
$ls4: 0.2rem;
$ls5: 0.375rem;
$ls6: 0.5rem;
$ls7: 0.65rem;
$ls8: 0.8rem;
$ls9: 1.0rem;
$ls10: 1.5rem;


// Colors
$white: #ffffff;
// rgba(255, 255, 255, 1);
$drkWhite: #edecec;
$drkBlue: #232c38;
$midBlue: #454f5c;
$ltBlue: #556ee6;
$grey: #5b6267;
//$midGrey: #8e9297;
$ltGrey: #b2b2b2;
$midGrey: $ltGrey;
$thinGrey: #bdc6ce;
$ltDark: $grey;

$drkGrey: $ltGrey;

$vBlue: #5ba3ce;
$blue: #4099ff;
$orange: #ff7043;

// Alerts & Notifications
$primary: $vBlue;
$secondary: #52637f;
$success: #59ce28;
$danger: #ef3749;
$warning: #efd23f;
$info: #14ad9f;
$light: #dfe5ef;

// Alerts & Notifications
$vBlue: rgba(8, 95, 167, 1);
$secondary: rgba(82, 99, 127, 1);
$success: rgba(89, 206, 40, 1);
$danger: rgba(239, 55, 73, 1);
$warning: rgba(239, 210, 63, 1);
$info: rgba(20, 173, 159, 1);
$light: rgba(223, 229, 239, 1);
$dark: rgba(44, 51, 59, 1);
$gold: #ab915d;
